.main-nav {
  background-color: #303f9f;
  border: none;
  border-radius: 0px;
}
.main-nav .navbar-brand {
  color: #ffc107;
  font-size: 24px;
}
.main-nav .navbar-brand:hover {
  color: #d39e00;
}
.main-nav .navbar-nav li a {
  color: #ffc107;
}
.main-nav .navbar-nav li a:hover {
  color: #d39e00;
}
.dashboard-tabs {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
}
.dashboard-tabs .tab {
  max-height: 350px;
  background-color: #3f51b5;
  min-width: 350px;
  margin: 10px;
  padding: 10px;
  cursor: pointer;
  box-shadow: 5px 5px 10px #888;
  border: 1px solid #3849a2;
  user-select: none;
}
.dashboard-tabs .tab:hover {
  background-color: #3b4caa;
  transform: scale(1.01);
}
.dashboard-tabs .tab .tab-icon {
  float: left;
}
.dashboard-tabs .tab .tab-icon .icon-circle {
  background-color: #ffc107;
  border-radius: 50%;
  width: 5em;
  height: 5em;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.dashboard-tabs .tab .tab-icon .icon-circle i {
  font-size: 2.5em;
  color: #fff;
}
.dashboard-tabs .tab .tab-text {
  float: right;
  color: #fff;
  font-size: 3em;
}
#editMenuTitleBtn {
  font-size: 0.4em;
  color: #727272;
}
#editMenuTitleBtn:hover {
  color: #212121;
  cursor: pointer;
}
.menu-items-section {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
}
.menu-items-section .menu-item {
  min-width: 30%;
  background-color: #c5cae9;
  border: 1px solid #a0a8db;
  box-shadow: 5px 5px 10px #888;
  padding: 10px;
  margin: 10px;
}
.event-list tbody tr {
  border-bottom: 1px solid #b6b6b6;
}
.event-list tbody tr:nth-child(even) td {
  color: #727272;
}
.event-list tbody tr:nth-child(even):hover {
  background-color: #d8dbf0;
}
.event-list tbody tr:nth-child(odd) {
  background-color: #c5cae9;
}
.event-list tbody tr:nth-child(odd) td {
  color: #fff;
}
.event-list tbody tr:nth-child(odd):hover {
  background-color: #b3b9e2;
}
body {
  font-family: 'Bree Serif', serif;
  background-color: #eaecf7;
}
.header-text {
  border-bottom: 3px solid #ffc107;
  color: #3f51b5;
}
